<template>
  <div>
    <a-modal
      v-model="DialogShowMeetingPerson"
      title="ข้อมูลการประชุมของท่าน"
      centered
    >
      <a-spin :spinning="spinning">
        <div>
          <v-row class="pt-3 pb-1 pt-0 pb-0">
            <v-col class="">
              <a-alert type="success" :description="this.convert_DateCendar" />
            </v-col>
          </v-row>
           <a-empty  v-show="this.empty_data == true"  style="margin-top: 20px" >
<span slot="description" > ท่านไม่มีประชุมในวันนี้ </span>
           </a-empty>

          <v-row
            class="pt-5"
            align="center"
            justify="center"
            v-for="items in this.data_list"
            :key="items"
          >
            <v-col class="pa-0">
              <a-card>
                <a-timeline>
                  <a-timeline-item color="green">
                    <a-icon
                      class="pr-5"
                      slot="dot"
                      type="solution"
                      style="font-size: 18px;"
                    />
                    {{ "หัวข้อประชุม : " + items.agenda }}
                  </a-timeline-item>
                  <a-timeline-item color="red">
                    <a-icon
                      slot="dot"
                      type="calendar"
                      style="font-size: 18px;"
                    />
                    {{ parseToDateFormat(items.meeting_start) }}
                  </a-timeline-item>

                  <a-timeline-item color="pink">
                    <a-icon slot="dot" type="bank" style="font-size: 18px;" />
                    {{ "ห้องประชุมที่ : " + items.room_num }}
                  </a-timeline-item>
                  <a-timeline-item color="blue">
                    <a-icon
                      slot="dot"
                      type="clock-circle-o"
                      style="font-size: 18px;"
                    />
                    {{
                      parseToTimeFormat(items.meeting_start, items.meeting_end)
                    }}
                  </a-timeline-item>

                  <a-timeline-item color="red">
                    <a-icon slot="dot" type="team" style="font-size: 18px;" />
                    <p>ผู้ร่วมประชุม :</p>
                    <div >
                      <a-tag class="mt-2" color="blue">
                        {{ items.one_email }}
                      </a-tag>
                    </div>
                    <div v-for="item_tag in items.guest_email" :key="item_tag">
                      <a-tag class="mt-2" color="blue">
                        {{ item_tag }}
                      </a-tag>
                    </div>
                  </a-timeline-item>
                </a-timeline>
              </a-card>
            </v-col>
          </v-row>
        </div>
      </a-spin>
      <template slot="footer">
        <a-button key="back" type="primary" @click="closeDialogShowMeetingPerson()">
          ปิด
        </a-button>
      </template>
    </a-modal>
    
<a-modal v-model="status_500" centered :footer="null">
    <a-result status="500" title="500" sub-title="Sorry, the server is wrong.">
    <template #extra>
    </template>
  </a-result>
</a-modal>
   

  </div>
</template>
<script>
export default {
  components: {},
  data: () => ({
    DialogShowMeetingPerson: false,
    convert_DateCendar: null,
    select_date_calender: null,
    spinning: false,
    user_login: "",
    data_list: null,
    empty_data: false,
    status_500:false,
    email_user:""
  }),
  created(){
   this.getdata_storage();
  },
  methods: {
    open(data) {
      this.select_date_calender = data;
      console.log(data);
      var dd = data.slice(8, 10);
      var mm = data.slice(5, 7);
      var yy = data.slice(0, 4);
      //   console.log(dd);
      //   console.log(mm);
      //   console.log(yy);
      this.convert_DateCendar = "วันที่ " + dd + "-" + mm + "-" + yy;
      this.API_ShowBookingUser(data);
      this.DialogShowMeetingPerson = true;
    },
    cleanSpinning() {
      this.spinning = false;
    },
    closeDialogShowMeetingPerson(){
      this.DialogShowMeetingPerson = false;
    },
    parseToDateFormat(dateString) {
      let date = new Date(dateString);

      return (
        "วันที่ : " +
        date.getDate() +
        "-" +
        (date.getMonth() + 1) +
        "-" +
        date.getFullYear()
      );
    },
    parseToTimeFormat(startTime, endTime) {
      let timeStart = new Date(startTime);
      let timeEnd = new Date(endTime);
      var timeStart_min = 0;
      var timeEnd_min = 0;
      if (timeStart.getMinutes() == 0) {
        timeStart_min = timeStart.getMinutes() + "0";
      } else {
        timeStart_min = timeStart.getMinutes();
      }
      if (timeEnd.getMinutes() == 0) {
        timeEnd_min = timeEnd.getMinutes() + "0";
      } else {
        timeEnd_min = timeEnd.getMinutes();
      }
      return (
        "เวลา : " +
        timeStart.getHours() +
        ":" +
        timeStart_min +
        " - " +
        timeEnd.getHours() +
        ":" +
        timeEnd_min +
        " น."
      );
    },
    API_ShowBookingUser(data) {
      this.spinning = true;
       this.empty_data = false;

      this.axios({
        method: "GET",
        url:
          this.$store.state.url_get_API_axios +
          "api/v1/bookingTable/" +
          this.email_user +
          "/time/" +
          data,
        // headers: { Authorization: "Bearer " + localStorage.token },
      })
        .then((response) => {
          console.log(response);

          var results = response.data;
          if (response.statusText == "OK") {
            if(response.data.length === 0){
                  this.empty_data = true;
                  this.cleanSpinning();
            }
                 this.data_list = results;
                 this.cleanSpinning();
          }
          else {
            this.$error({
              centered: true,
              title: "ไม่พบข้อมูล",
              content: "กรุณาตรวจสอบข้อมูล",
            });
            this.cleanSpinning();
          }
          this.cleanSpinning();
        })
        .catch((error) => {
          console.log(error.response);
          // if (error.response.data == "Page Not Found") {
          //   this.$message.error("ไม่พบข้อมูล");
          //   this.cleanSpinning();
          // } else if (error.response.data.Status == "Page Not Found") {
          //   this.$error({
          //     centered: true,
          //     title: "ไม่พบข้อมูล",
          //     content: "กรุณาตรวจสอบข้อมูล",
          //   });
          //   this.cleanSpinning();
          // } 
          // else if(error.response.statusText == "Internal Server Error"){
          //       this.status_500 = true
          // } 
          // else {
          //   this.$message.error("เกิดข้อผิดพลาด");
          // }
          this.cleanSpinning();
        });
    },
       getdata_storage() {
      let data = JSON.parse(window.localStorage.getItem("user_data"));

      if (data === null) {
        this.getdata = null;
      } else {
        this.email_user = data.email;
      }
    },
  },
};
</script>
<style scoped>
.spin-content {
  border: 1px solid #91d5ff;
  background-color: #e6f7ff;
  padding: 30px;
}
</style>
